.app-wrapper {
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 60px 1fr;
  grid-template-areas:
    "header header"
    "nav content";
  grid-gap: 10px;

  font-family: 'Roboto', sans-serif;
	font-size: 15px;
  /* font-weight: 400; */
	line-height: 25px;
	letter-spacing: 0.1px;
	color: black;
}
.app-wrapper-content {
  background-color: #791216;
}

